// export default ObsolescenceWidgetToolbarNativeCta;


import { useState } from "react";
import Popup from "../../components/Popup/Popup";
import useToast from "../../hooks/useToast";
import { MSG_BOS_SAVE } from "../../utils/toastMessages";
import MassUpdateDropdown from "./MassUpdateDropdown";
import { useSelector } from "react-redux";
import useObsolescenceWidget from "../../hooks/useObsolescenceWidget";
import { fetchCsrfToken } from "../../services/api/PlantAssignment/fetchCsrfService";
import {fetchEnoviaWebService} from "../../utils/helpers";

const ObsolescenceWidgetToolbarNativeCta = ({
  onSave,
  state,
  selectedRows,
  tableData,
  onMassUpdate,
  type,
  latestRevision,
  droppedRevision,
  ParentItemState,
  cardData
}) => {
  const { showWarningToast, showErrorToast } = useToast();
  const [showDropdown, setShowDropdown] = useState(false);
  const{obsoleteStateChecking} = useObsolescenceWidget()
  const parentObjectData = useSelector(
    (state) => state.droppedObject?.obselenceObjectData?.parentObjectData || []
  );
  const handleSaveClick = () => {
    if (type !== "Document" && latestRevision !== droppedRevision) {
      showWarningToast(MSG_BOS_SAVE);
    } else {
      onSave();
    }
  };

  console.log("cardData from Parent component", cardData)
  // const handleObsoleteClick = () => {
  //   // Show the new toast
  //   showWarningToast("Product is not yet released");

  //   // Preserve original save logic
  //   handleSaveClick();
  // };

  const handleObsoleteClick = async () => {
    console.log("Parent Object Data:", parentObjectData);
    if(parentObjectData){
      const hasUnreleasedItems = parentObjectData.some(item => item?.state !== "RELEASED");
      const hasInvalidChangeIntent = parentObjectData.some(
        item => item?.changeIntent[0]?.what !== "ChangeMaturityObsolete"
      );

      if (hasUnreleasedItems) {
        showErrorToast("All items must be in 'RELEASED' state.");
        return;
      } else if (hasInvalidChangeIntent) {
        showErrorToast("All items must have ChangeIntent: 'ChangeMaturityObsolete'.");
        return;
      }
      await obsoleteStateChecking(parentObjectData, cardData);

      const headers = await fetchCsrfToken();
      let id = cardData?.cardData?.['Dropped Revision ID'];
      let response;
      const apiURL =`https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dseng/dseng:EngItem/${id}/dslc:changeControl?$mask=dsmvlc:ChangeControlMask.Status`;
      await fetchEnoviaWebService("GET",apiURL,{}, headers).then (async (apiResponse) => {
        console.log("API Response:", apiResponse);
        if (apiResponse.status === true) {
          let changeControlStatus = JSON.parse(apiResponse.data).member[0]?.['Change Control Status'];
          if (changeControlStatus !== "ANY" && changeControlStatus !== undefined){
            const changeactionapiURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/changeaction/${changeControlStatus}?$fields=proposedChanges`;
            await fetchEnoviaWebService ("GET", changeactionapiURL,{}, headers).then(async (changeactionResponse) => {
              console.log("changeactionResponse:", changeactionResponse);
              if (changeactionResponse.status === true) {
                let res= JSON.parse(changeactionResponse.output);
                if( res.proposedChanges[0].whats[0].what === "ChangeMaturityObsolete" ){
                  console.log("what response is",res.proposedChanges[0].whats[0].what);
                }else{
                  console.log("ress",res.proposedChanges)
                  }
                }
        }).catch((error) => {
          console.log("Error in catch", error);
          response = error;
          showErrorToast("Something went wrong");
          return response;
        });
          }else {
            showErrorToast("something went wrong changecontrol status is not correct");
          }
    }
   
  }).catch((error) => {
    console.log("Error in catch", error);
    response = error;
    showErrorToast("Something went wrong....");
    return response;
  });
    
    }
  };
  const handleMassUpdate = (column, value, scope) => {
    if (scope === "selected") {
      onMassUpdate(selectedRows, column, value);
    } else {
      onMassUpdate(tableData, column, value);
    }
  };

  const editableColumns = [
    "Print On Purchase Order Required",
    "Print On Work Order Required",
    "Work Order Document Required",
    "Print On report Order Required",
    "SAP/JDE",
  ];

  return (
    <div className="d-flex flex-column cta-absolute">
      <div className="d-flex">
        <button
          className="btn btn-outline-success btn-lg m-2"
          onClick={handleObsoleteClick}
        >
          Obsolete
        </button>
        {/* <button
          className="btn btn-outline-primary btn-lg m-2"
          onClick={() =>
            type !== "Document" && latestRevision !== droppedRevision
              ? showWarningToast(MSG_BOS_SAVE)
              : setShowDropdown((prev) => !prev)
          }
        >
          Mass Update
        </button> */}
      </div>

      {showDropdown && (
        <div className="mt-2">
          <MassUpdateDropdown
            editableColumns={editableColumns}
            selectedRows={selectedRows}
            onApplyUpdate={handleMassUpdate}
            valueOptions={["Yes", "No"]}
            onClose={() => setShowDropdown(false)}
          />
        </div>
      )}
    </div>
  );
};

export default ObsolescenceWidgetToolbarNativeCta;
